import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HeaderComponent } from './navBar/header/header.component';
import { SideBarComponent } from './navBar/side-bar/side-bar.component';
import { FooterComponent } from './navBar/footer/footer.component';
import { BookBorrowComponent } from './bookProcess/book-borrow/book-borrow.component';
import { BookReturnComponent } from './bookProcess/book-return/book-return.component';
import { ViolationHandleComponent } from './bookProcess/violation-handle/violation-handle.component';
import { BookManagementComponent } from './dataProcess/book-management/book-management.component';
import { CardManagementComponent } from './dataProcess/card-management/card-management.component';
import { ClientManagementComponent } from './dataProcess/client-management/client-management.component';
import { BookBorrowReturnReportComponent } from './report/book-borrow-return-report/book-borrow-return-report.component';
import { UserManagementComponent } from './sysManagement/user-management/user-management.component';


const routes: Routes = [
  {path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  {path: 'dashboard', component: SideBarComponent },
  {path: 'book-borrow', component: BookBorrowComponent },
  {path: 'book-return', component: BookReturnComponent },
  {path: 'violation-handle', component: ViolationHandleComponent },
  {path: 'book-management', component: BookManagementComponent },
  {path: 'card-management', component: CardManagementComponent },
  {path: 'client-management', component: ClientManagementComponent },
  {path: 'book-borrow-return-report', component: BookBorrowReturnReportComponent },
  {path: 'user-management', component: UserManagementComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
